#result-display{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

#result-display__title {
  font-size: 1.5rem;
  font-weight: 600;
  margin: 0;
}

.result-display__row {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  align-items: center;
}

.result-display__details {
  display: flex;
  flex-direction: row;
  gap: 1rem;

  font-weight: 500;
}

.result-display__details__number {
  font-weight: 500;
  color: #007fff;
}

#result-display__error{
  text-align: center;
  color: gray;
}
